<template>
  <div class="examinee-exam">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <p class="title-box">
        批次名称：<span class="mr40">{{$route.query.batch}}</span>
        当前考点：<span>{{$route.query.name || '未分配考点'}}</span>
      </p>
      <div class="btn-block">
        <div @click="$exportDetail('/export/rStudentInExamExport.do')" class="btn-main">导出</div>
        <div @click="showDialog" class="btn-main-deepen ml15">调整考点</div>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="7%">
            <el-checkbox v-show="checkAll !== null" v-model="checkAll"></el-checkbox>
            <span v-show="checkAll === null">-</span>
          </th>
          <th width="10%">姓名</th>
          <th width="15%">身份证号码</th>
          <th width="15%">报考类别</th>
          <th width="13%">联系电话</th>
          <th width="20%">学校所在区域</th>
          <th width="10%">录取结果</th>
          <th width="10%">操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item,key) in table.list">
          <td>
            <el-checkbox v-show="item.isEnroll !== 'YES'" v-model="checkboxList[key]"></el-checkbox>
            <span v-show="item.isEnroll === 'YES'">-</span>
          </td>
          <td>{{item.name}}</td>
          <td>{{item.idCard || '--'}}</td>
          <td>{{item.firstRecruitType}}/{{item.secondRecruitType}}</td>
          <td>{{item.phone}}</td>
          <td>{{item.fullAddress || '--'}}</td>
          <td>{{$enums.RecruitStudentStatus.getName(item.studentStatus) || '--'}}</td>
          <td>
            <span @click="jumpExamDetail(item)" class="oper">详情</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
      <!--调整考点弹窗-->
      <el-dialog title="调整考点" class="dialog" :visible.sync="dialog" width="450px">
        <p class="title">选择学生数：{{center.studentIdList.length}}</p>
        <p class="title">当前分配考点：{{center.oldExamCentreName}}</p>
        <span class="title">调整考点：</span>
        <el-select filterable default-first-option class="input" v-model="center.newExamCentreId">
          <el-option v-for="item in examCenterList"
                     :value="item.id" :key="item.id" :label="item.name"
          ></el-option>
        </el-select>
        <div class="btns">
          <div @click="setCenter" class="btn-submit mr20">确认</div>
          <div @click="dialog = false" class="btn-clear">取消</div>
        </div>
      </el-dialog>
    </main-part>
  </div>
</template>
<script>
  import mixins from '../../../assets/js/mixins'
  export default {
    mixins: [mixins.$exportDetail],
    data() {
      return {
        nav: [{name: "考试管理", path: "/recruit/exam"},{name: "查看考点", path: "go(-1)"},{name: "查看考生", path: ""}],
        hide: true,
        searchData: [
          {type: 'select',title: '录取结果', model: 'isEnroll', option: this.$enums.YesNoStatus.list},
          {type: 'input', placeholder: '姓名/身份证号码'}
        ],
        // 表格多选框
        checkboxList:[false],
        table:{
          url: '/rstudent/queryStudentPageInExamCentre.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query:{
            isEnroll: null,
            examCentreId: this.$route.query.id
          }
        },
        //
        dialog: false,
        // 当前批次下的考点
        examCenterList: [],
        // 考点
        center:{
          oldExamCentreId: this.$route.query.id,
          oldExamCentreName: this.$route.query.name,
          newExamCentreId: null,
          studentIdList: []
        }
      }
    },
    watch:{
      'table.list':function () {
        this.checkboxList = new Array(this.table.list.length).fill(false)
      }
    },
    computed: {
      checkAll: {
        get: function () {
          if(this.table.list.length < 1){
            return false
          }else if(this.table.list.every(item => item.isEnroll === 'YES')){
            return null
          }
          for(let i = 0;i< this.table.list.length; i++){
            let n = this.table.list[i]
            if(n.isEnroll !== 'YES' && this.checkboxList[i] === false){
              return false
            }
          }
          return true
        },
        set: function (val) {
          let arr = []
          for(let i = 0;i< this.table.list.length; i++){
            let n = this.table.list[i]
            if(n.isEnroll === 'YES'){
              arr[i] = false
            }else{
              arr[i] = val
            }
          }
          this.checkboxList = arr
        }
      }
    },
    methods: {
      // 前往考生详情页
      jumpExamDetail(item){
        this.$router.push({
          path: '/recruit/examinee/' + item.id,
          query:{
            type: item.recruitType
          }
        })
      },
      // 获取当前批次下的考点
      async getExamCenter(){
        this.examCenterList = await this.$ajax({
          url: '/centre/queryExamCentreList.do',
          query:{
            batchId: this.$route.query.batchId,
          }
        })
      },
      // 展现弹窗
      showDialog(){
        let num = 0
        this.center.studentIdList = []
        this.checkboxList.forEach((e,i) => {
          if(e){
            num++
            this.center.studentIdList.push(this.table.list[i].id)
          }
        })
        if(num > 0){
          this.dialog = true
        }else{
          this.$message.warning('请先选择考生')
        }
      },
      // 调整考点
      async setCenter(){
        await this.$ajax({
          url: '/centre/batchDistributionCentreStudent.do',
          query: this.center
        })
        this.$search(this.table)
        this.dialog = false
      }
    },
    mounted(){
      // 如果是手动调整
      if(this.$route.query.hand + '' === 'true'){
        this.table.url = '/rstudent/queryStudentPageUnabsorbed.do'
        this.table.query = {
          isEnroll: null,
          batchId: this.$route.query.batchId
        }
        this.searchData.splice(0,1)
      }
      this.$search(this.table)
      this.getExamCenter()
      // 取消选择所有的多选框
      this.checkboxList = new Array(this.table.list.length).fill(false)
    }
  }
</script>
<style lang="sass" scoped>
  .examinee-exam
    .title-box
      position: absolute
      line-height: 30px
      top: 17px
    .dialog
      .title
        margin-bottom: 20px
      .input
        margin-left: 20px
        width: 300px
      .btns
        margin-top: 25px
        text-align: center
</style>
